import React, { Component } from 'react'
import Axios from 'axios';
import logo from '../../logo1.svg'
import './PostFull.css'
import uuid from 'uuid'
import {Link} from 'react-router-dom'
import Comments from './Comments';
import $ from 'jquery'
export class PostFull extends Component {

    state= {
        post: {},
        isFullPost:true,
        loaded:false,
        comments:[],
        body:'',
        user:'reader',
        dev:false,
        submitted:false
    }
    
    componentWillMount(){
        console.log(window.location.pathname)
        if(  /\/(\w+)\/(\w+)?/.exec(window.location.pathname)[2]){
        Axios.get(
            this.state.dev?'http://localhost'+window.location.pathname:'/api'+ window.location.pathname).then(v=> {
            this.setState({post:v.data,loaded:true,
            comments:v.data.comments})
        })}
        else {
            window.location.replace('/postlist')
        }
        
    }
    onChange = (e) => {
    this.setState({[e.target.id]:e.target.value})// this.setState({[e.targed.id]: e.target.value})   
    }

    onSubmit= (e)=> {
        e.preventDefault()
        let query = '?post=' + this.state.post.slug
        Axios.post(this.state.dev? 'http://localhost/comment'+query:'/api/comment'+query, {user: this.state.user, body:this.state.body}).then((v)=> {
            e.preventDefault()
            this.setState({submitted: true})
            console.log(v.data)
        }).catch(console.log)
    }
       someGuy = function (stringer,load) {

       function formatText (index=0, str, len=0, text,  stringret= '|'){
            
            
           function WordCount(str) { 
                return str.split(" ").length;
            }
            let isComplete=false
            while(!isComplete&&str) {
              let count = WordCount(str.slice(0,str.indexOf('.')+1 ))
              len+=count
              stringret+= str.slice(0,str.indexOf('.')+1 )
              str=str.slice(str.indexOf('.')+1 ,str.length)
              
           
              
              
           
              if(len>100){
                  stringret+='|'
              }
            if (len>=WordCount(text)||str.indexOf('.')===-1){isComplete=true
               //return stringret+str
              
           }
           }
           return( (stringret+str))
           }
         let billy=  formatText(0, stringer,0 ,stringer).split("|")
      
       return billy.map(i=> (<p key={uuid()}>{i}</p>))
       }
       getCom =(i)=> {
           console.log(i)
           return i.map(t=> (
            <div className="media mb-4">
                    <img className="d-flex mr-3 rounded-circle" src="http://placehold.it/50x50" alt=""/>
                    <div className="media-body">
                    <h5 className="mt-0">{t.user.toUpperCase()}</h5>
                            {t.body}
                    </div>
          </div>
           ))
       }
    onClick= () =>{
       // alert('pp')
       $("#togIcon").toggleClass("flip")
       $("#togCom").toggleClass("showComments")
    }
  render() {
      let {post:p,loaded, comments:i} = this.state
    return (
      <div>
            <header className="header postHead" >
                <Link to="/"><div className= 'logo'><img style={{width:'90px'}} alt="logo" src={logo}/>
                    <h2 id="siteHead">ColourfulLanguage</h2>
                    </div>
                </Link>

                <nav className="nav">
                    <li className="nav-item">
                    <Link to="/" className="nav-link" href="lion">Home</Link>
                    </li>
                    <li className="nav-item">
                    <Link to="/" className="nav-link" href="lion" >About</Link>
                    </li>
                </nav>
            </header>
                
            <div className="post" >
                <div className="postHeader" style={{height:"40vh",width:"100%", backgroundImage:`url(/${p.image})`}}>
                    <div className="overlay"></div>
                </div>
                <div className="postTitleDiv">
                    <div className="container">
                        <div className="row" style={{border:" 1px solid rgba(0,0,0,.125)", borderBottom:"none", marginTop:"10px", paddingTop:"10px",paddingLeft:"20px"}}>
                            <h1  style={{display:'inline-block', width:"50%"}}>{p.title}</h1>
                            <div className="postData" style={{display:'inline-block', width:"50%", padding:"10px",textAlign:"right"}}>
                                <small style={{display:"block", margin:"5px"}}>
                                    <i className="fa fa-user" aria-hidden="true" style={{marginRight:'5px'}}></i>by author</small>
                                <span className="badge  "><i  className="fa fa-anchor" aria-hidden="true"></i></span>
                                <span className="badge ">
                                    <i className="fa fa-beer" aria-hidden="true"></i></span>
                                <span className="badge">  <i className="fa fa-bell" aria-hidden="true"></i></span>
                                <div className='hidden'>
                                    <p>
                                        <i className="fa fa-anchor" style={{marginRight:'5px'}} aria-hidden="true"></i>Post is a core one
                                    </p>
                                    <p>
                                        <i className="fa fa-beer" style={{marginRight:'5px'}} aria-hidden="true"></i>Post is a chill
                                    </p>
                                    <p>
                                        <i className="fa fa-bell" style={{marginRight:'5px'}} aria-hidden="true"></i>My name is not babak 
                                    </p>
                                </div>
                                </div>
                        </div>
                    </div>
                    
                </div>
                <div className="container post-cont" style={{border:" 1px solid rgba(0,0,0,.125)", borderBottom:"none", borderTop:"none"}}>
                    <div className="half">
                        
                            <article id="post-article-text"  className="post-body">
                                    {
                                       this.someGuy(p.body,loaded)
                                       
                                    }
                                
                               
                            </article>
                            <div className="card my-4">
                                <h5 className="card-header" style={{backgroundColor:'inherit', border:'none'}}>Leave a Comment:</h5>
                                <div className="card-body">
                                <form onSubmit={this.onSubmit}  id="commentform">
                                    <div className="form-group">
                                    <textarea onChange={this.onChange} id="body" name="body" className="form-control" rows="3"></textarea>
                                    <input onChange={this.onChange} id="user" style={{marginTop:"10px"}} className="form-control" type="text" name="id" placeholder="Name"/>
                                    <small className="helpText" >You can leave your email or name or phone number</small>
                                    </div>
                                    <button type="submit" className="btn btn-primary">Submit</button>
                                    {this.state.submitted? (<strong style={{color:'Green'}}>Submitted</strong>):''}
                                </form>
                                </div>
                            </div>
                            <div className="commentsDiv">


                                
                                    <div className="container">
                                  
                                    <button onClick={this.onClick} className="btn" data-toggle="collapse" data-target="#collapseOne"aria-expanded="true" aria-controls="collapseOne">
                                    <h5 className=" hideComments showComments" style={{display:"inline-block",margin:"10px 0 10px 10px", padding:"12px"}} id="togCom"></h5> <i id="togIcon" className="fa fa-angle-down" aria-hidden="true"></i>
                                                </button>
                                    <hr/></div>
                                    <div className="collapse" id="collapseOne" >
                                    {
                                         i.map(t=> (
                                             
                                            <Comments key={uuid()} comment={t}/>
                                           ))
                                        }
                                    </div>
                                
                              
                            </div>
                        
                    </div>
                    <div className="half">
                        
                            <div className="list-group">
                                    <h5 style={{textAlign:'right'}} ><i className="fa fa-link" aria-hidden="true" ></i><span style={{marginLeft:'5px'}}>Quick links</span></h5>
                                    <button className="list-group-item list-group-item-action ">
                                        Active item</button>
                                    <hr style={{width:'80%',marginBottom:'0', marginTop:'0' ,marginRight:'0',float:'right'}}/>
                                    <button className="list-group-item list-group-item-action">Item</button>
                                    <hr style={{width:'80%',marginBottom:'0', marginTop:'0' ,marginRight:'0',float:'right'}}/>
                                    <button className="list-group-item list-group-item-action">Disabled item</button>
                            </div>
                        </div>
                </div>
            </div>
        
      </div>
    )
  }
}

export default PostFull
